import { Component, OnInit } from '@angular/core';
import { BannerService } from '../../../../../auth/_services/banner.service';
import { MenuService } from '../../../../../auth/_services/menu.service';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { Helpers } from '../../../../../helpers';
import { DataService } from '../../../../../_services/data.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Attributes } from '../../../../../auth/_models/attributes';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from "@angular/router";

@Component({
    selector: 'app-add-slider',
    templateUrl: "add-slider.component.html",
    styles: []
})
export class AddSliderComponent implements OnInit {

    form: FormGroup;
    private formSumitAttempt: boolean;


    public subCategoryData = "";
    public searchValue = "";
    public dropDownValue = '';
    public noOfPage = '';
    public menuDrpdown: any;
    public productData: any;
    public categoryDrpdown = '';
    public selectedMenu = '';
    public selectedStatus = '';
    public selectedDiscountType = '';
    public selectedCategory = '';
    selectedItems = [];
    public sliderImgUrl = '';
    public selectedsliderType = '';



    public menuData = "";
    dropdownSettings = {};


    /* public menuDrpdown = "";
     public searchValue = "";
     public dropDownValue = "";
     public productType = "";*/


    constructor(private toastr: ToastrService, private router: Router, private _bannerService: BannerService, private _menuService: MenuService, private formBuilder: FormBuilder) { }


    ngOnInit() {

        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        if (permission.website_setting != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }
        /*user permission*/

        this.menuData = JSON.parse(localStorage.getItem('menu'));

        console.log('product_data');
        this._bannerService.getProduct().subscribe(
            data => {
                this.productData = data.product;
            }
        );


        this.dropdownSettings = {
            singleSelection: false,
            idField: 'item_id',
            textField: 'item_text',
            selectAllText: 'Select All',
            unSelectAllText: 'UnSelect All',
            itemsShowLimit: 3,
            allowSearchFilter: true
        };




        this.form = this.formBuilder.group({
            tab_title: [null, Validators.required],
            left_title: [null, Validators.required],
            product_id: [null, Validators.required],
            time: [null, Validators.required]
        });
    }


    isFieldValid(field: string) {
        return (
            (!this.form.get(field).valid && this.form.get(field).touched) ||
            (this.form.get(field).untouched && this.formSumitAttempt)
        );
    }

    displayFieldCss(field: string) {
        return {
            'has-error': this.isFieldValid(field),
            'has-feedback': this.isFieldValid(field)
        };
    }

    onSubmit() {
        let input: any;
        this.formSumitAttempt = true;
        if (this.form.valid) {
            input = { form_data: this.form.value };
            /*input.form_data.slider_img = this.sliderImgUrl;
            console.log(input);*/
            this._bannerService.createSlider(input).subscribe(
                data => {
                    // this.subCategoryData = data.success;
                    this.menuDrpdown = data.menu;
                    this.toastr.success('slider Added Successfully', 'Success Message');
                    this.reset();
                }
            );
        }
    }

    reset() {
        if (this.selectedsliderType != '' && this.selectedStatus != '') {
            this.form.reset();
            this.selectedsliderType = '';
            this.selectedStatus = '';
            this.formSumitAttempt = false;
        }
    }

    handleFileInput(files: FileList) {
        let file = files.item(0);
        var reader = new FileReader();
        reader.onload = (event: any) => {
            this.sliderImgUrl = event.target.result;
        }
        reader.readAsDataURL(file);
    }

    onItemSelect(item: any) {
        // this.selectedItems.push(item);
        console.log(this.selectedItems);
    }

    onSelectAll(items: any) {
        console.log(items);
    }

    onDeSelectItem(item: any) {
        console.log(this.selectedItems);
    }






}
